@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap");
.text-dark {
  color: #48556a;
}

.bg-dark {
  background-color: #48556a;
}

.text-light {
  color: #ecf2f8;
}

.bg-light {
  background-color: #ecf2f8;
}

.text-desaturated {
  color: #6d7f97;
}

.bg-desaturated {
  background-color: #6d7f97;
}

.text-gray {
  color: #9eafc2;
}

.bg-gray {
  background-color: #9eafc2;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  background-color: #ecf2f8;
  font-family: "Manrope", sans-serif;
  max-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drawer {
  min-width: 296px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.drawer img {
  position: absolute;
  min-width: 362px;
  width: 100%;
  object-fit: contain;
}

.information {
  margin: 2.1875rem 2.5rem 1.875rem 2.1875rem;
}
.information .article {
  margin-bottom: 1.25rem;
}
.information .article h1 {
  font-size: 1.25rem;
  margin-bottom: 0.9375rem;
}
.information .article p {
  font-size: 0.8125rem;
  line-height: 1.25rem;
}
.information .writer {
  display: flex;
  justify-content: space-between;
}
.information .writer__information {
  display: flex;
}
.information .writer__information img {
  width: 2.1875rem;
  height: 2.1875rem;
  border-radius: 50%;
  margin-right: 0.9375rem;
}
.information .writer__information__detail {
  font-size: 0.8125rem;
}
.information .writer__information__detail h2 {
  font-size: 0.8125rem;
}
.ToolTip {
  background-color: #48556a;
}

button {
  cursor: pointer;
}

.toggle-social-media {
  position: relative;
}

.balloon {
  padding: 50px;
  position: absolute;
  background-color: #48556a;
  min-width: 224px;
  min-height: 66px;
  display: none;
  right: calc(100vw - 800px);
  z-index: 1000;
}

.balloon.active {
  display: block;
  animation: fade_in_show 0.3s;
}

@keyframes fade_in_show {
  0% {
    opacity: 0;
    transform: translateY(10%);
  }
  100% {
    opacity: 1;
  }
}
.active a {
  color: aqua;
}

.btn {
  outline: none;
  width: 2.1875rem;
  height: 2.1875rem;
  border-radius: 50%;
  border: none;
}
.btn::after {
  content: url(assets/images/icon-share.svg);
  width: 0.9375rem;
}

main {
  display: flex;
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 1.25rem 1.875rem rgba(158, 175, 194, 0.25);
  width: 100%;
  max-width: 45rem;
  overflow: hidden;
}

.attribution {
  font-size: 11px;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.attribution a {
  color: #3e52a3;
}